.main {
    display: block;
    margin: 0 auto;
}

.header {
    position: fixed;
    width: 100%;
    padding-bottom: 20px;
    border-bottom: 1px solid darkgrey;
    background-color: #FBF8F1;
}

.footer {
    position: fixed;
    bottom: 0;
    background-color: #F7ECDE;
    width: 100%;
    padding-bottom: 10px;
}

.scroll-check {
    display: block;

    width: 100%;
    height: 10px;
}
